import React, {useEffect} from "react";
import {Page} from "framework7-react";
import {Loader} from "../components/Loader";

export interface InitProps {
    onInit(): void
}

export const Init: React.FC<InitProps> = (props: InitProps) => {

    useEffect(() => {
        setTimeout(() => {
            props.onInit();
        }, 2000);
    }, [])

    return (
        <Page>
            <Loader/>
        </Page>
    )
}
